import React from 'react'
import { Breadcrumb } from 'antd'
import { NavLink } from 'react-router-dom'

const Breadcrumbs = () => (
  <Breadcrumb
    items={[
      {
        title: <NavLink to='/home'></NavLink>
      },
      {
        title: <p></p>
      }
    ]}
  />
)
export default Breadcrumbs
